  display: block;
  height: 1.5rem;
  width: 1.5rem;
  border: 1px solid #484848;
  border-radius: 50%;
  transition: border .22s ease; }
  .theme-switcher__trigger:after {
    content: " ";
    position: absolute;
    bottom: -0.5rem;
    transform: translateY(0.625rem);
    height: 0.125rem;
    background-color: transparent;
    width: 100%;
